// 1
import sunriseAndSunsetData from "./sunrise-sunset.json";

export const getMoment = (locationName) => {
  // 2
  const location = sunriseAndSunsetData.find((data) => {
    return data.CountyName === locationName;
  });

  // 3
  if (!location) {
    throw new Error(` ${location} `);
  }

  // 4
  const now = new Date();

  // 5 "2019-10-08" 
  const nowDate = Intl.DateTimeFormat("zh-TW", {
    year: "numeric",
    month: "2-digit",
    day: "2-digit",
  })
    .format(now)
    .replace(/\//g, "-");

  // 6
  const locationDate = location?.time.find((time) => time.Date === nowDate);

  // 7
  if (!locationDate) {
    throw new Error(` ${locationName}  ${nowDate} `);
  }

  // 8TimeStamp
  const sunriseTimestamp = new Date(
    `${locationDate.Date} ${locationDate.SunRiseTime}`
  ).getTime();
  const sunsetTimestamp = new Date(
    `${locationDate.Date} ${locationDate.SunSetTime}`
  ).getTime();
  const nowTimeStamp = now.getTime();

  // 9
  return sunriseTimestamp <= nowTimeStamp && nowTimeStamp <= sunsetTimestamp
    ? "day"
    : "night";
};

