//
import React, { useState, useEffect, useCallback, useMemo } from "react";
// emotion  styled
import styled from "@emotion/styled";
// themeProvider   props
import { ThemeProvider } from "@emotion/react";
import "dayjs/locale/zh-tw";

//
import WeatherCard from "./views/WeatherCard";
import WeatherSetting from "./views/WeatherSetting";

//css
import "./App.css";
import "normalize.css"; //
//hook
import useWeatherAPi from "./hooks/useWeatherAPi";

// utils
import { getMoment, findLocation } from "./utils/helpers";

//-----------------------------------------------------------------------------
const theme = {
  light: {
    backgroundColor: "#ededed",
    foregroundColor: "#f9f9f9",
    boxShadow: "0 1px 3px 0 #999999",
    titleColor: "#212121",
    temperatureColor: "#757575",
    textColor: "#828282",
  },
  dark: {
    backgroundColor: "#1F2022",
    foregroundColor: "#121416",
    boxShadow:
      "0 1px 4px 0 rgba(12, 12, 13, 0.2), 0 0 0 1px rgba(0, 0, 0, 0.15)",
    titleColor: "#f9f9fa",
    temperatureColor: "#dddddd",
    textColor: "#cccccc",
  },
};

//
//style.div div  button styled.button

const Container = styled.div`
  ${"" /* callback ({theme})=>{} */}
  background-color: ${({ theme }) => theme.backgroundColor};
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
`;

function App() {
  const [currentTheme, setCurrentTheme] = useState("dark");

  //
  const [currentPage, setCurrentPage] = useState("WeatherCard");
  const handleCurrentPageChange = (currentPage) => {
    setCurrentPage(currentPage);
  };

  // method 1.
  // const storedCity = localStorage.getItem("cityName") || "";

  // const [currentCity, setCurrentCity] = useState(storedCity);

  // method 2.  useState  state

  const [currentCity, setCurrentCity] = useState(
    () => localStorage.getItem("cityName") || ""
  );

  const handleCurrentCityChange = (currentCity) => {
    setCurrentCity(currentCity);
  };

  const currentLocation = useMemo(
    () => findLocation(currentCity),
    [currentCity]
  );
  // {
  //   cityName: "",
  //   locationName: "",
  //   sunriseCityName: "",
  // },

  const { cityName, locationName, sunriseCityName } = currentLocation;

  const moment = useMemo(() => getMoment(sunriseCityName), [sunriseCityName]);

  const [weatherElement, fetchData] = useWeatherAPi({ locationName, cityName });

  useEffect(() => {
    setCurrentTheme(moment === "day" ? "light" : "dark");
  }, [moment]);

  return (
    <ThemeProvider theme={theme[currentTheme]}>
      <Container>
        {currentPage === "WeatherCard" && (
          <WeatherCard
            cityName={cityName}
            weatherElement={weatherElement}
            moment={moment}
            fetchData={fetchData}
            handleCurrentPageChange={handleCurrentPageChange}
          />
        )}
        {currentPage === "WeatherSetting" && (
          <WeatherSetting
            handleCurrentPageChange={handleCurrentPageChange}
            handleCurrentCityChange={handleCurrentCityChange}
            cityName={cityName}
          />
        )}
      </Container>
    </ThemeProvider>
  );
}

export default App;
